.App {
    padding: 30px;
    display: flex;
}

.App > div {
    margin-right: 50px;
}

.task-done {
    text-decoration: line-through;
    opacity: 0.7;
}

.activeButton {
    background-color: yellow;
    border: 2px solid yellow;
}
.error {
    outline: 0;
}

.errorInput {
    border: 2px solid red;
    outline: none;
}
input:focus {
    outline: none;
}